.btn {
  display: inline-flex;
  padding: 12px 32px;

  font-weight: 500;
  color: $color-default-white;

  background-color: $color-cinnabar;
  border: none;
  cursor: pointer;

  transition: background-color $trans-default, box-shadow $trans-default,
    border $trans-default;

  &--red {
    background-color: $color-torch-red;
  }

  &--primary {
    background-color: $color-cinnabar;
  }

  &--secondary {
    background-color: rgba($color: $color-cinnabar, $alpha: 0.15);
    border: 1px solid rgba($color: $color-cinnabar, $alpha: 0.8);
  }

  &--md {
    padding: 16px 42px;

    @include vp-1023 {
      padding: 16px 40px;
    }

    @include vp-374 {
      padding: 14px 34px;
    }
  }

  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }

  @include hover-focus {
    background-color: $color-scarlet;
    box-shadow: 0px 2px 24px 0px rgba(255, 30, 30, 0.5);
  }
}
